___CSS_LOADER_EXPORT___.push([module.id, `.nav {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px;
}

.nav-links {
    display: flex;
    gap: 16px;
}

.nav-link {
    color: #007bff; /* Цвет текста */
    text-decoration: none;
    font-size: 16px;
    position: relative;
    padding-bottom: 4px; /* Подсказка для пространства под текстом */
    transition: color 0.3s ease;
}

.nav-link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #007bff;
    transition: width 0.3s ease;
}

.nav-link:hover {
    color: #0056b3; /* Более темный оттенок при наведении */
}

.nav-link:hover::after {
    width: 100%; /* Подчеркнуть ссылку при наведении */
}`, "",{"version":3,"sources":["webpack://./src/shared/navBar/navBar.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,cAAc,EAAE,gBAAgB;IAChC,qBAAqB;IACrB,eAAe;IACf,kBAAkB;IAClB,mBAAmB,EAAE,2CAA2C;IAChE,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,OAAO;IACP,SAAS;IACT,QAAQ;IACR,WAAW;IACX,yBAAyB;IACzB,2BAA2B;AAC/B;;AAEA;IACI,cAAc,EAAE,uCAAuC;AAC3D;;AAEA;IACI,WAAW,EAAE,qCAAqC;AACtD","sourcesContent":[".nav {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 16px;\n}\n\n.nav-links {\n    display: flex;\n    gap: 16px;\n}\n\n.nav-link {\n    color: #007bff; /* Цвет текста */\n    text-decoration: none;\n    font-size: 16px;\n    position: relative;\n    padding-bottom: 4px; /* Подсказка для пространства под текстом */\n    transition: color 0.3s ease;\n}\n\n.nav-link::after {\n    content: '';\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 0;\n    height: 2px;\n    background-color: #007bff;\n    transition: width 0.3s ease;\n}\n\n.nav-link:hover {\n    color: #0056b3; /* Более темный оттенок при наведении */\n}\n\n.nav-link:hover::after {\n    width: 100%; /* Подчеркнуть ссылку при наведении */\n}"],"sourceRoot":""}]);
