    display: flex;
    align-items: center;
    gap: 8px; /* Расстояние между кнопками */
}

.toggle-button {
    padding: 8px 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #f0f0f0;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 14px;
}

.toggle-button:hover {
    background-color: #e0e0e0;
}

.toggle-button.active {
    background-color: #007bff; /* Цвет активной кнопки */
    color: #fff;
    border-color: #007bff;
}

.toggle-button.inactive {
    color: #555;
